import Vue from 'vue'
import Router from 'vue-router'
import login from '@/pages/login.vue'
import register from '@/pages/register.vue'
import reset from '@/pages/reset.vue'
import admin from '@/pages/admin.vue'
import papers from '@/pages/papers.vue'
import account from '@/pages/account.vue'
import paperCreate from '@/pages/paperCreate.vue'
import paperEdit from '@/pages/paperEdit.vue'
import paper from '@/pages/paper.vue'
import categoryCreate from '@/pages/categoryCreate.vue'
import categoryEdit from '@/pages/categoryEdit.vue'
import category from '@/pages/category.vue'
import report from '@/pages/report.vue'
import API from '@/API'

Vue.use(Router)

const router = new Router({ 
  mode: 'hash',
  routes: [
    { path: '/', redirect: '/login' },
    { path: '/login', component: login, name:'' }, 
    { path: '/register', component: register, name:'' }, 
    { path: '/reset', component: reset, name:'' }, 
    { path: '/admin', component: admin, name:'' }, 
    { path: '/admin/account', component: account, name:'' }, 
    { path: '/admin/papers', component: papers, name:'' },     
    { path: '/admin/paper/create', component: paperCreate, name:'' }, 
    { path: '/admin/paper/edit/:id', component: paperEdit, name:'' }, 
    { path: '/admin/paper/:id', component: paper, name:'' }, 
    { path: '/admin/paper/:id/category/create', component: categoryCreate, name:'' }, 
    { path: '/admin/paper/:id/category/edit/:query', component: categoryEdit, name:'' }, 
    { path: '/admin/paper/:id/category/:query', component: category, name:'' }, 
    { path: '/admin/paper/:id/report/:query', component: report, name:'' }, 
  ]
})

router.beforeEach( async(to, from, next) => {

  document.title = to.name

  next()
})

export default router



